// -----------------------------------------------------
// Description :
// provide variables and mixin for alowed background-colors
// -----------------------------------------------------
// Resposability :
// use for background colors,
// or gradients backgrounds if required
// -----------------------------------------------------
// Dependancies :
// _settings_colors.scss,
// -----------------------------------------------------

/*------------------------------------*\
            BACKGROUND COLORS
\*------------------------------------*/

$background-colors: (
    lighter: getTint($snow, light),
    premium: getTint($gold, medium),
    alert: getTint($red, lighter)
);

@mixin background-color($color) {
    background-color: map-get($background-colors, $color);
};
