// -----------------------------------------------------
// Description :
// provide pub position
// -----------------------------------------------------
// Dependencies :
// _settings_global.scss
// _settings_step.scss
// _settings_text.scss
// _settings_font-families.scss
// _settings_background-color.scss
// _settings_borders.scss
// ----------------------------------------------------

.cm-ad-position {
    @include background-color (lighter);
    @include border($side: top, $color: light);
    @include border($side: bottom, $color: light);
    @include font-family(std);
    padding: step(s) step(s) step(l);

    &__info {
        @include color(light);
        display: block;
        margin-bottom: step(xs);
    }

    &__container {
        max-width: 100%;
        width: 300px;
        margin: 0 auto;
    }
}
