// -----------------------------------------------------
// Description :
// provide all brand logos
// -----------------------------------------------------
// Dependancies :
// _settings_global.scss,
// _tools_images.scss,
// -----------------------------------------------------

.ca-logo {

    @include background-img;
    @include hide-text;
    height: 23px;


    &--std {
        width: 99px;
        background-image: svg("logo-lmd");
    }

    &--premium {
        width: 187px;
        background-image: svg("logo-lmd-premium");
    }
}

