// -----------------------------------------------------
// Description :
// provide values expressed in px.
// -----------------------------------------------------
// Responsabilities :
// use for margin / padding, vertical or horizontal spaces
// margin-left: pas(s); or margin-left: ($some-value + pas(m));
// use it for heights or widths, if the concerned element
// height or with correspond to an empty space
// @mixin bodymargin give you the external content margin
// -----------------------------------------------------
// Segregation :
// step(x) : do not use for other units, like font-sizes etc...
// $content-margin :  do not use for margins not releated to
// main content zone horizontal margin,
// even if the value is the same.
// -----------------------------------------------------
// Dependancies :
// _settings_global.scss,
// -----------------------------------------------------

$steps : (
    'xl': $base-step * 6,  // 24
    'l': $base-step * 5,   // 20
    'm': $base-step * 4,   // 16
    's': $base-step * 3,   // 12
    'xs': $base-step * 2,  // 8
    'xxs': $base-step,     // 4
);

@function step($size) {
    @return map-get($steps, $size);
}

// expose horizontal content margins value
// for calculation purpose only
$content-margin: step(xl);

// set horizontal content margins
@mixin content-margin() {
    padding-left: $content-margin;
    padding-right: $content-margin;
}

// reset horizontal content margins
@mixin reverse-content-margin() {
    margin-left: (- $content-margin);
    margin-right: (- $content-margin);
}
