// -----------------------------------------------------
// Description :
// provide all site icons
// -----------------------------------------------------
// Dependencies :
// _tools_images.scss
// _tools_helpers.scss
// -----------------------------------------------------

@mixin ico-svg($width, $height, $align: baseline) {
    @include background-img;
    @include hide-text;
    width: $width;
    height: $height;
    vertical-align: $align;
}

.ca-ico {

    &--check-mark {
        @include ico-svg(16px, 12px);
    }

    &--key {
        @include ico-svg(18px, 10px);
    }

    &--premium-content {
        @include ico-svg(16px, 16px);
    }

    &--time {
        @include ico-svg(15px, 16px,text-bottom);
    }
}
