// -----------------------------------------------------
// Description :
// an article view (generaly part of a list) in tiny format
// image and text in front of each other
// -----------------------------------------------------
// Dependancies :
// _settings_global.scss
// _settings_text.scss
// -----------------------------------------------------

.cm-short-view {
    display: block;
    padding-top: step(s);
    padding-bottom: step(xl);
    line-height: 0; // remove lh from wrapping <a> tag
    text-decoration: none;

    &__desc {
        @include content-margin;
        @include line-height(std);
        padding-top: step(m);
    }

    &__media {
        width: 100%;
        height: auto;
    }

    &__standfirst {
        padding-top: step(m);
    }
}
