// -----------------------------------------------------
// Description :
// provide classes for styled lists
// -----------------------------------------------------
// Responsability :
// provide styling for lists (bulleted, ordered …)
// -----------------------------------------------------
// Segregation :
// do not use for unstyled lists with no bullets and margins styles
// do not set font-size or font styling
// (this should be setted in a higher level component)
// -----------------------------------------------------
// Dependancies :
// _settings_global.scss
// _settings_text.scss
// -----------------------------------------------------

.cm-list-bullet {
    &__item {
        display: flex;
        flex-flow: row nowrap;
        align-items: flex-start;

        &::before {
            content: "";
            display: inline-block; //for IE 11 windows < 10 and android < 4.4 bug on flex on pseudo elements
            flex: initial;
            background-repeat: no-repeat;

            .cm-list-bullet--check & {
                min-width: 16px;
                min-height: 12px;
                margin-right: step(s);
                margin-top: 0.4em; // align with text relative to font-size
            }

        }

        &:not(:last-child) {
            margin-bottom: step(xs);
        }
    }
}

.cm-list-bordered {
    &__item {
        @include border($side: bottom, $width: std, $color: light);
        @include content-margin;
        padding-top: step(m);
        padding-bottom: step(m);
    }
}
