/* hide a text sending it out of its container */
@mixin hide-text {
    text-indent: 100%;
    overflow: hidden;
    white-space: nowrap;
    color: transparent;
}

/* dimensions */
@mixin size($width, $height) {
    width: $width;
    height: $height;
}
