@function getTint($set, $variant) {
    $variantes: (
        darker: 5,
        dark: 4,
        medium: 3,
        light: 2,
        lighter: 1,
    );

    $i: map-get($variantes, $variant);
    // caculate the diférentes between max and min value to get increment
    // for saturation
    $increment-sat: (map-get($set, max-sat) - map-get($set, min-sat)) / (length($variantes) - 1);
    // for  lightness
    $increment-lum: (map-get($set, max-light) - map-get($set, min-light)) / (length($variantes) - 1);

    @return hsl(
        map-get($set, hue),
        map-get($set, min-sat) + $increment-sat * ($i + 1),
        map-get($set, max-light) - ($increment-lum * ($i - 1))
    )
}

$slate: (
    hue: 220,
    min-sat: 8%,
    max-sat: 15%,
    min-light: 20%,
    max-light: 45%
);

$smoke: (
    hue: 220,
    min-sat: 8%,
    max-sat: 10%,
    min-light: 50%,
    max-light: 85%
);

$snow: (
    hue: 220,
    min-sat: 15%,
    max-sat: 15%,
    min-light: 90%,
    max-light: 99%
);

$gold: (
    hue: 45,
    min-sat: 95%,
    max-sat: 98%,
    min-light: 35%,
    max-light: 80%
);

$red: (
    hue: 0,
    min-sat: 80%,
    max-sat: 90%,
    min-light: 40%,
    max-light: 99%
);
