// -----------------------------------------------------
// Description :
// provide classes for headings
// -----------------------------------------------------
// Responsability :
// set font-family and font-size for each instance,
// eventualy font-weight (only for system fonts)
// and line-height.
// -----------------------------------------------------
// Segregation :
// do not set colors => they should be contextualy setted
// -----------------------------------------------------
// Dependancies :
// _settings_global.scss
// _settings_text.scss
// _settings_font-families.scss
// -----------------------------------------------------

.ca-heading {

    &-primary {
        @include font-family('serif');
        @include font-size(xxl);
        font-weight: 700;
    }

    &-xl {
        @include font-family('serif');
        @include font-size(xl);
        font-weight: 700;
    }

    &-l {
        @include font-family('serif');
        @include font-size(xl);
        font-weight: 800;
    }

    &-sub {
        @include font-family('std');
        @include font-size(xl);
        font-weight: 700;
    }
}
