// -----------------------------------------------------
// Description :
// provide a grid system
// -----------------------------------------------------
// Responsability :
// to create columns with same size in height
// -----------------------------------------------------
// Extension :
// do not use shortcuts for flex values
// to ensure ie11 support
// -----------------------------------------------------
// Dependancies :
// _settings_global.scss
// _settings_step.scss
// -----------------------------------------------------

.ol-grid {
    $gutter-width: step(m);
    display: flex;
    align-items: flex-start;
    position: relative;
    flex-flow: row wrap;

    > .ol-grid {
        left: 0;
        right: 0;

        &.ol-grid__cell {
            width: 100%;
        }
    }

    &--gutter {
        margin-left: - ($gutter-width / 2);
        margin-right: - ($gutter-width / 2);
    }

    &--gutter > &__cell {
        padding-left: $gutter-width / 2;
        padding-right: $gutter-width / 2;
    }

    &__cell {
        // by default a ol-grid__cell keep his natural width

        &--fill {
            // ie11 require units, the px is required
            flex: 1 1 0px;
        }

        &--full {
            flex: 1 100%;
            &\@small {
                @media (max-width: 600px) {
                    flex: 1 100%;
                }
            }
        }

        &--1of2 {
            flex: 0 0 50%;
            max-width: 50%; // ie11
            &\@large {
                @media (min-width: 601px) {
                    flex: 0 0 50%;
                    max-width: 50%; // ie11
                }
            }
        }

        &--1of3 {
            flex: 0 0 33.333%;
            max-width: 33.333%; // ie11
            &\@large {
                @media (min-width: 601px) {
                    flex: 0 0 33.333%;
                    max-width: 33.333%; // ie11
                }
            }
        }
    }
}
