// -----------------------------------------------------
// Description :
// Main bundle
// -----------------------------------------------------
// Responsabilities :
// import all files required by default across framework
// -----------------------------------------------------

/*------------------------------------*\
                SETTINGS
\*------------------------------------*/
// Globally-available variables and config.
@import '_settings_global';
// colors sets.
@import '_settings_colors';
// imported font families.
@import '_settings_font-families';

// Colors / font sizes / familly.
@import '_settings_text';
// generate solid borders and border-radius declarations.
@import '_settings_borders';
// function that return alowed sapcing values.
@import '_settings_step';
// generate background colors declarations.
@import '_settings_background-colors';

/*------------------------------------*\
                TOOLS
\*------------------------------------*/
// helpers mixins and functions. To be called first.
@import '_tools_helpers';
// images mixins and functions.
@import '_tools_images';

/*------------------------------------*\
                GENERIC
\*------------------------------------*/
// Corrects some default browser display
@import '_generic_normalize';
// Set default display for tag elements
@import '_generic_reset';
// Set default styles globally
@import '_base_global';
// Set default styles for tag elements
@import '_base-atoms_text';

/*------------------------------------*\
                OBJECTS
\*------------------------------------*/
// for layout containers
@import '_object-layout_containers';
// grid system
@import '_object-layout_grid';

/*------------------------------------*\
                ATOMS
\*------------------------------------*/
// buttons
@import '_component-atom_button';
// brand logos.
@import '_component-atom_logo';
// classes for headings
@import '_component-atom_headings';
// landmarks that give contextual informations
// about an associated content
@import '_component-atom_marks';
// classes for dividers
@import '_component-atom_divider';
// icons.
@import '_component-atom_icons';

/*------------------------------------*\
                MOLECULES
\*------------------------------------*/
// figure => privide media + legend group
@import '_component-molecule_figure';
// listed article with inlined image and text
@import '_component-molecule_tiny-view';
// listed articles with larges images and titles
@import '_component-molecule_short-view';
// style header blocks for subcontents (modules / sections)
@import '_component-molecule_block-header';
// ads
@import '_component-molecule_ads';
// provide lists styles (bulleted / oredered… etc)
@import '_component-molecule_list';
// provide lists styles (bulleted / oredered… etc)
@import '_component-molecule_block-header';

/*------------------------------------*\
                ORGANISMS
\*------------------------------------*/
// website header
@import '_component-organism_header';
// outbrain sugestions
@import '_component-organism_outbrain';
// article element
@import '_component-organism_article';
// describe a premium content and sugest the user to go premium
@import '_component-organism_teasing';

/*------------------------------------*\
                UTILITIES
\*------------------------------------*/
// Utilities
@import '_trumps_utilities';
// Margin and padding utility classes generation
@import '_trumps_spaces';
