// -----------------------------------------------------
// Description :
// an article view (generaly part of a list) in tiny format
// image and text in front of each other
// -----------------------------------------------------
// Dependancies :
// _settings_global.scss
// _settings_text.scss
// -----------------------------------------------------

.cm-tiny-view {
    @include font-size(s);
    display: block;
    width: 100%;
    line-height: 0; // remove lh from wrapping <a> tag to avoid bottom margin
    text-decoration: none;

    &--alert {
        @include color(alert);
        @include background-color(alert);
    }

    &__desc {
        @include line-height(std); // reset lh inherited from <a> .cm-tiny-view
        font-weight: 400;
    }

    &__media {
        width: 100%;
        height: auto;
    }

    &__pub-time {
        @include color(lighter);
        word-break: keep-all;
        white-space: nowrap;

        .cm-tiny-view--alert & {
            @include color(alert-light);
        }
    }

    &__icon {
        margin-right: step(xxs);
        vertical-align: middle;
    }

    &__tag {
        @include color(light);
        @include font-size(xxs);
        margin-bottom: step(xxs);
        text-transform: uppercase;

        .cm-tiny-view--alert & {
            @include color(alert);
        }
    }
}
