// -----------------------------------------------------
// Description :
// provide outbrain articles list
// -----------------------------------------------------
// Dependancies :
// _settings_global.scss
// _settings_background-color.scss
// _settings_steps.scss
// -----------------------------------------------------

.co-outbrain {
    @include content-margin();
    @include background-color(lighter);
    padding-top: step(xl);
    padding-bottom: step(xl);

    &__heading {
        display: block;
        padding-bottom: step(l);
    }

    &__item:not(:last-child) {
        display: block;
        padding-bottom: step(xl);
    }

    &__desc {
        @include line-height(std);
    }

    &__media {
        width: 100%;
        height: auto;
    }

    &__title {
        font-weight: bold;
    }

    &__link {
        @include font-size(s);
        display: block;
        line-height: 0; // remove lh from wrapping <a> tag
        text-decoration: none;
    }
}
