// -----------------------------------------------------
// Description :
// import custom font-families
// -----------------------------------------------------

@font-face {
    font-family: 'theantiqua';
    src: url('../fonts/theantiquab-w5plain-webfont.woff2') format('woff2'),
         url('../fonts/theantiquab-w5plain-webfont.woff') format('woff');
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: 'theantiqua';
    src: url('../fonts/theantiquab-w5plainitalic-webfont.woff2') format('woff2'),
         url('../fonts/theantiquab-w5plainitalic-webfont.woff') format('woff');
    font-weight: 400;
    font-style: italic;
}

@font-face {
    font-family: 'theantiqua';
    src: url('../fonts/theantiquab-w7bold-webfont.woff2') format('woff2'),
         url('../fonts/theantiquab-w7bold-webfont.woff') format('woff');
    font-weight: 700;
    font-style: normal;
}

@font-face {
    font-family: 'theantiqua';
    src: url('../fonts/theantiquab-w7bolditalic-webfont.woff2') format('woff2'),
         url('../fonts/theantiquab-w7bolditalic-webfont.woff') format('woff');
    font-weight: 700;
    font-style: italic;
}

@font-face {
    font-family: 'theantiqua';
    src: url('../fonts/theantiquab-w8extrabold-webfont.woff2') format('woff2'),
         url('../fonts/theantiquab-w8extrabold-webfont.woff') format('woff');
    font-weight: 800;
    font-style: normal;
}
