// -----------------------------------------------------
// Description :
// a familly for labels/tags/flags or other textual
// landmarks that give contextual informations about
// an associated content. ex: content category
// -----------------------------------------------------
// Responsability :
// atoms marks should only set
//  - font-size, font-famlilly
//  - NO text colors unless the mark set his hown background-color
// -----------------------------------------------------
// Dependancies :
// _settings_global.scss,
// _tools_text.scss,
// -----------------------------------------------------

.ca-mark {
    &-tiny {
        @include font-size(xxs);
        @include font-family(std);
        text-transform: uppercase;
    }
}
