// -----------------------------------------------------
// Description :
// provide UI buttons
// -----------------------------------------------------
// Dependancies :
// _settings_global.scss,
// _settings_borders.scss,
// _settings_text.scss,
// _settings_steps.scss
// -----------------------------------------------------

.ca-btn {
    @include border-radius(std);
    @include color(std);
    @include border(std);
    @include font-size(l);
    @include font-family(std);
    display: block;
    width: 100%; // for input and buttons tags
    padding: (step(xs) - 2) step(m) step(xs);
    text-align: center;
    font-weight: bold;
    line-height: 1.2em;
    text-decoration: none;
    background: none;
    cursor: pointer;

    &--premium {
        @include border(transparent); // keep the same height
        @include background-color(premium);
    }

    // sub text for multiline buttons
    &__sub {
        @include font-size(xs);
        display: block;
        margin-bottom: 0;
        text-transform: uppercase;
        letter-spacing: 0.05em;
        font-weight: 300;
    }
}
