// -----------------------------------------------------
// Description :
// blocks containing titles
// - like a sidebar header
// - a listed article module header ex
// -----------------------------------------------------
// Responsability :
// - space the elements
// - do not use for editorial sections
// -----------------------------------------------------
// Segregation :
// - do not use for pages headers
// - do not use for editorial sections
// -----------------------------------------------------
// Dependancies :
// _settings_global.scss
// -----------------------------------------------------

.cm-block-header {

    &-primary {
        @include content-margin;
        padding-bottom: step(xs);
        padding-top: step(m);

        &::after {
            @include border($side: bottom, $width: m, $color: dark);
            padding-bottom: step(xs);
            content: "";
            display: block;
        }
    }

    &-secondary {
        @include border($side: bottom, $width: std, $color: light);
        @include content-margin;
        padding-bottom: step(m);
        padding-top: step(m);
    }
}
