// -----------------------------------------------------
// Description :
// provide a molecule with a media image and a legend
// -----------------------------------------------------
// Dependancies :
// _settings_global.scss
// _settings_text.scss
// _settings_step.scss
// -----------------------------------------------------

.cm-figure {
    &__media {
        width: 100%;
        height: auto;
    }

    &__caption {
        @include font-size(s);
        @include line-height(s);
        @include color(light);
        @include font-family(std);
        padding: step(xxs) step(xl);
    }

    &__credit {
        @include line-height(std);
        white-space: nowrap;
        text-transform: uppercase;
    }
}
