// -----------------------------------------------------
// Description :
// élément article
// -----------------------------------------------------
// Dependencies :
// _settings_global.scss
// _settings_step.scss
// _settings_colors.scss
// _settings_text.scss
// _settings_font-families.scss
// _settings_borders.scss
// -----------------------------------------------------

.co-article {

    @include content-margin();

    &__header {
        @include border($side: bottom, $color: light);
        @include font-size(s);
        padding: step(m) 0 step(s);
    }

    &__pub-date {
        @include color(light);
    }

    &__headline {
        @include line-height(xs);
        margin: step(s) 0;
    }

    &__standfirst {
        @include border($side: bottom, $color: light);
        @include font-size(l);
        @include font-family(std);
        padding: step(m) 0;
    }

    &__body {
        @include font-size(l);
        @include font-family(serif);
        padding-top: step(m);

        p:not(:last-child) {
            margin: 0 0 step(l);
        }
    }

    &__subtitle {
        margin: step(xl) 0 step(s);
    }

    &__media {
        @include reverse-content-margin();
        margin-bottom: step(l);
    }

    &__quotation {
        @include font-family(serif-ital);
        @include border($side: left, $width: 3px, $color: light);
        padding-left: step(m);
        margin-bottom: step(l);
    }

    &__footer {
        padding: step(m) 0;
    }
}
