// -----------------------------------------------------
// Description :
// an interface that provide a premium content description to the user
// and sugest him to subscribe and go premium / buy the article
// -----------------------------------------------------
// Dependancies :
// _settings_global.scss
// -----------------------------------------------------

.co-teasing {
    @include content-margin;
    padding-top: step(l);
    padding-bottom: step(l);

    &__connect {
        @include font-size(s);
        @include border($side: bottom, $color:light);
        padding-bottom: step(m);
    }

    &__advantages {
        @include font-size(l);
        @include font-family(serif);
        padding: step(m) 0;
    }

    &__sub-title {
        padding-bottom: step(m);
    }

    &__lock-text {
        padding-bottom: step(xs);
    }
}
