// -----------------------------------------------------
// Description :
// provide variables and mixins for text
// -----------------------------------------------------
// Responsabilities :
// store and export alowed text declarations ( @import font-size(std) )
// -----------------------------------------------------
// Segregation :
// do not store or export multiple declarations.
// Only one couple of propertie/value
// -----------------------------------------------------
// Dependancies :
// _settings_global.scss,
// -----------------------------------------------------

/*------------------------------------*\
                FONT SIZE
\*------------------------------------*/

$font-sizes: (
    xxl: 24px,
    xl: 19px,
    l: 16px,
    m: 15px,
    std: 14px,
    s: 13px,
    xs: 12px,
    xxs: 11px
);

@mixin font-size($size) {
    font-size: map-get($font-sizes, $size);
};

/*------------------------------------*\
                COLORS
\*------------------------------------*/
$colors: (
    'std': getTint($slate, medium),
    'med': getTint($slate, light),
    'light': getTint($smoke, dark),
    'lighter': getTint($smoke, light),
    'alert': getTint($red, dark),
    'alert-light': getTint($red, light)
);

@mixin color($color) {
    color: map-get($colors, $color);
};

/*------------------------------------*\
            LINE-HEIGHT
\*------------------------------------*/

$line-heights: (
    std: 150%,
    s: 125%,
    xs: 110%
);

@mixin line-height($lh) {
    line-height: map-get($line-heights, $lh);
};

/*------------------------------------*\
            FONT-FAMILIES
\*------------------------------------*/

$font-families: (
    'std': $font-family-std,
    'serif': $font-family-serif
);

@mixin font-family($ff) {
    font-family: map-get($font-families, $ff);
};
