// -----------------------------------------------------
// Description :
// generate utility classes for margins and paddings following steps variables.
// -----------------------------------------------------
// Dependencies :
// _settings_step.scss,
// -----------------------------------------------------

$space-properties: (
    'margin'
);

$space-directions: (
    '-bottom'
);

$space-sizes: (
    '-s': step(s),
    '-m': step(m),
    '-l': step(l)
);

@each $property in $space-properties {

    @each $direction in $space-directions {

        @each $size, $value in $space-sizes {

            .u-#{$property}#{$direction}#{$size} {
                #{$property}#{$direction}: $value;
            }
        }
    }
}
